@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap);
@media (min-width: 1200px) {
  .App { width: 1200px; }
}

@media (min-width: 625px) and (max-width: 1199px){
  .App { width: 600px; }
}

@media (max-width: 599px){
  .App { width: 100%; }
}

body {
  margin: 0;
  font-family: 'Rubik', sans-serif;
}


.App {
  margin: 0 auto;
  display: grid;
  grid-template-rows: -webkit-min-content -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content min-content 1fr min-content;
  grid-template-areas: "header" "tags" "main" "footer";
  min-height: 100vh;
  max-height: 100vh;
  /*background: cadetblue;*/
}

h2 {
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
}

h3 {
  font-size: 20px;
}
@media (min-width: 300px) and (max-width: 1199px){
    .Header_logo__IodpV { order: 1; }
    .Header_login__3fzia { order: 2; }
    .Header_search__2Q65- { order: 3; flex-basis: 60% !important; margin-left: auto; margin-right: auto;}
}

.Header_wrapper__TYFA_ {
    grid-area: header;
    display: flex;
    justify-content: space-between;
    min-height: 80px;
    flex-wrap: wrap;
}

.Header_logo__IodpV {
    /*background-color: #2F80ED;*/
    display: flex;
    align-items: center;
    flex-basis: 33% ;
}

.Header_logo__IodpV span {
    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
    margin-left: 16px;
}

.Header_search__2Q65- {
    /*background-color: #56CCF2;*/
    flex-basis: 33%;
    align-self: center;
    display: grid;
    grid-template-columns: 1fr -webkit-min-content;
    grid-template-columns: 1fr min-content;
}

.Header_searchField__C5t5B {
    outline: none;
    border: none;
    border-bottom: 1px solid black;
    padding-bottom: 5px;
}

.Header_searchButton__Y9FVf {
    margin-left: 10px;
    color: #E5261E;
    font-weight: 500;
    cursor: pointer;

}

.Header_login__3fzia {
    /*background-color: #F2C94C;*/
    flex-basis: 33%;
    align-self: center;
    text-align: right;
    font-size: 16px;
    font-weight: 500;
}

.Header_logout__2UkQT {
    margin-left: 10px;
    color: #E5261E;
    cursor: pointer;
}


.Nav_wrapper__2ghlJ {
    grid-area: tags;
    /*background: burlywood;*/
    display: flex;
    justify-content: center;
}

.Nav_tab__1WqfQ {
    margin: 0 10px;

    font-weight: 500;
    font-size: 28px;

    text-decoration: none;
}

.Nav_tab__1WqfQ div {
    padding-bottom: 5px;
    margin-bottom: 5px;
}

.Nav_tab__1WqfQ {
    color: black;
}

.Nav_tab__1WqfQ.Nav_active__3ZHMy {
    color: #E5261E;
}

.Nav_active__3ZHMy div {
    border-bottom: solid red 1px;
}
.Footer_wrapper__3eqx6 {
    grid-area: footer;
    background: #F2F2F2;
    overflow: hidden;
    display: grid;
    grid-template-columns: 100px 1fr;
}

.Footer_logo__5EEHk {
    width: 32px;
    height: 64px;
    margin: 28px;
}

address {
    align-self: center;
    font-size: 16px;
    font-style: normal;
}

.Footer_telephones__l6kej a {
    text-decoration: none;
    color: black;
}


